            addItem (item, index, idx) {
                var _this = this;
                idx != 4 && this.car.checkItem.push({
                    name: item.name,
                    value: item.value
                });
                switch (idx) {
                    case 1:
                        _this.car.brand.forEach(function (ee) {
                            ee.active = false;
                        })
                        item.active = true;
                        _this.activeItem = _this.car.checkItem.length + 1;
                        break;
                    case 2:
                        _this.car.carXiList.forEach(function (ee) {
                            ee.active = false;
                        })
                        item.active = true;
                        _this.activeItem = _this.car.checkItem.length + 1;
                        break;
                    case 3:
                        _this.car.carXiList.forEach(function (ee) {
                            ee.active = false;
                        })
                        item.active = true;
                        _this.activeItem = _this.car.checkItem.length + 1;
                        break;
                    case 4:
                        _this.car.carTypeList.forEach(function (ee) {
                            ee.active = false;
                        })
                        item.active = true;
                        break;
                }
            },
            //返回数据给后端
            gotoBack () {
