            },

            closeItem (index) {
                var _this = this;
                switch (index) {
                    case 0:
                        _this.activeItem = "1";
                        break;
                    case 1:
                        _this.activeItem = "2";
                        break;
                    case 2:
                        _this.activeItem = "3";
                        break;
                }
                this.car.checkItem.splice(index, 1);
                this.car.brand.forEach(function (item) {
                    item.active = false;
                })
                this.car.carXiList.forEach(function (item) {
                  item.active = false;
                })
            }
