    color: #5681C2;
    i{
      color: #5681C2;
    }
  }
}

.part-choose {
  margin-bottom: 14px;
  li {
    display: inline-block;
    label {
      margin-right: 10px;
    }
  }
  li.part-sort {
    margin-right: 40px;
  }
  li .el-select {
    width: 100px;
    background: #FAFCFF;
    .el-input--suffix {
      .el-input__inner {
        background: #FAFCFF;
      }
    }
  }
  li.brand-choose {
    margin-right: 100px;
  }
  li.part-search {
    .el-input {
      width: 390px;
    }
    .el-input--suffix {
      .el-input__inner {
        padding-right: 10px;
      }
    }
    .el-button--mini {
      padding: 7px 12px;
      position: relative;
      top: 1px;
    }
  }
  li.right {
    float: right;
  }
}

.tire-content {
  margin-top: 14px;
  background: #fff;
  padding: 15px 14px;
  color: #6E6E6E;
  .el-table {
    border: 1px solid #DBE4ED;
    border-radius: 0 4px 4px 4px;
  .cell {
          text-overflow: ellipsis;
          word-break: break-all;
          white-space: nowrap;
        width: 100%;
      }
    th>.cell {
      color: #2E2E2E;
      font-size: 14px;
    }

  }
  .el-table__body-wrapper {
    overflow: hidden;
  }
  .el-tabs__header {
    margin: 0;
    border-bottom: 0;
  }
  .el-tabs--card>.el-tabs__header .el-tabs__nav {
    border: none;
    border-radius: 4px 4px 0 0;
  }
  .el-tabs--card>.el-tabs__header .el-tabs__item:first-child {
    border: 1px solid #C1CCD8;
  }
  .el-tabs__item {
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
    border: 1px solid #C1CCD8;
    border-radius: 4px 4px 0 0;
    font-size: 14px;
    color: #6E6E6E;
    margin-right: 2px;
  }
  .el-tabs--card>.el-tabs__header .el-tabs__item.is-active {
    border: 1px solid #5681C2;
    font-size: 14px;
    color: #5681C2;
  }
  .el-tabs--top.el-tabs--card .el-tabs__item:nth-child(2) {
    padding-left: 10px;
  }
  .input-number {
    width: 120px;
    .el-input-number__decrease,
    .el-input-number__increase {
      width: 30px;
      font-size: 13px;
      background: transparent;
    }
    .el-input__inner {
      border-radius: 0;
    }
  }
  .el-table__body-wrapper {
    overflow: hidden;
  }
}

.footer {
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
}

.selected-list {
  margin-bottom: 14px;
  color: #999999;
}

.selected-list i {
  font-size: 12px;
  margin-right: 10px;
}

.selected-list h3 {
  display: inline-block;
  font-family: 'MicrosoftYaHei-Bold';
  font-weight: bold;
}

.delete {
  color: #5681C2;
  text-decoration: underline;
}

.tire-footer {
  overflow: hidden;
  margin-top: 16px;
}

.table-footer-btn {
  padding-bottom: 44px;
  text-align: center;
  button:first-child {
    margin-right: 50px;
  }
  button:last-child {
    margin-left: 50px;
  }
}


</style>



