            //弹窗关闭的时候做的动作
            dialogClose () {
//                console.log("fuck you")
//                var item = this.pageData.vinDetail;
//                for (var i in item) {
//                    item[i] = "";
//                }
                this.car.carXiList.forEach(function (item) {
