                });
                this.car.brand.forEach(function (item) {
                    item.active = false;
                });
                this.car.checkItem = [];
                this.active = null;
                this.activeItem = "1";
            },
            checkType (item, param, index) {
                var _this  = this;
                param.forEach(function (ee) {
                    ee.active = false;
                })
                item.active = true;
                switch (index) {
                    case '1':
                        _this.level.two = true;
                        break;
                    case '2':
                        _this.level.three = true;
                        break;
                    case '3':
                        _this.level.four = true;
                        break;
                }
            },
            //查看按钮
            handleClick (item) {
                this.tableData.list = this.tableData.orList;
            },
            clickSelectType (param) {
                this.dialog.typeSelect = true;
            },
            toggleContent (param) {
                console.log(param)
                if (param == "1" ) return;
                if (param == "2") {
                    this.activeItem = "2";
                    this.stepList[1].active  = true;
                    this.stepList[2].active  = false;
                    this.stepList[3].active  = false;
                }
                if (param == "3") {
                    this.activeItem = "3";
                    this.stepList[param-1].active  = true;
                    this.stepList[param].active  = false;

                } else if (param == "4")  {
                    this.activeItem = "4";
                    this.stepList.forEach(function (item) {
                        item.active = true;
                    });
                }


            },
            addItem (item, index, idx) {
                var _this = this;
                idx != 4 && this.car.checkItem.push({
                    name: item.name,
                    value: item.value
                });
                    switch (idx) {
                        case 1:
                            _this.car.brand.forEach(function (ee) {
                                ee.active = false;
                            })
                            item.active = true;
                            _this.activeItem = _this.car.checkItem.length + 1;
                            break;
                        case 2:
                            _this.car.carXiList.forEach(function (ee) {
                                ee.active = false;
                            })
                            item.active = true;
                            _this.activeItem = _this.car.checkItem.length + 1;
                            break;
                        case 3:
                            _this.car.carXiList.forEach(function (ee) {
                                ee.active = false;
                            })
                            item.active = true;
                            _this.activeItem = _this.car.checkItem.length + 1;
                            break;
                        case 4:
                            _this.dialog.typeSelect = false;
                            _this.pageData.vinShow = true;
                            _this.pageData.vinDetail.brand = _this.car.checkItem[0].name;
                            _this.pageData.vinDetail.carXi = _this.car.checkItem[1].name;
                            _this.pageData.vinDetail.carType = _this.car.checkItem[3].name;
                            console.log(this.car.checkItem);
                            break;
                    }
//                }
            },
            closeItem (index) {
                var _this = this;
                switch (index) {
                    case 0:
                        _this.activeItem = "1";
                        break;
                    case 1:
                        _this.activeItem = "2";
                        break;
                    case 2:
                        _this.activeItem = "3";
                        break;
                }
                this.car.carXiList.forEach(function (item) {
                   item.active = false;
                });
                this.car.brand.forEach(function (item) {
                    item.active = false;
                });
                this.car.checkItem.splice(index, 1);
            },
            //点击搜索VIN号
            searchBook () {
                if (this.pageData.vinNum.length < 17) {
                    this.$message({message: '请输入17位VIN号', type: 'warning'});
                    return;
                }
                this.pageData.vinShow = true;
            },
            //点击左边的树，右边表格刷新
            clickRefresh (param) {
                var tableSmj = [];
                this.tableData.list.forEach(function (item, index) {
                    var tableL = {};
                    for (var i in item) {
                        tableL[i] = item[i];
                    }
                    tableL.brand = "宝马" + parseInt(Math.random() * 10);
                    tableSmj.push(tableL);
                })
                this.tableData.list = tableSmj;
            },
            //展开或者收起
            showMore () {
                if (!this.style.more) {
                    var _this = this;
//                    this.style.height = "300";
                    this.style.more = true;
                    this.tableData.smjArr.forEach(function (item) {
                        _this.tableData.list.push(item);
                    })

                } else {
//                    this.style.height = "180";
                    this.style.more = false;
                    this.tableData.list = this.tableData.smjArr;
                }
            },
            handleSelectionChange (val, param) {
                this.tableData.selectList = val;
            },
            fixSelectionChange (val, param) {
                this.tableData.fixList = val;
            },
            deleteSelect (row, param) {
                var _this = this;
                this.$confirm('是否确定删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    _this.$refs[param].toggleRowSelection(row, false);
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {

                });
            }
        },
