        .el-dialog {
            width: 1010px; margin: 15vh auto; height: 600px; position: relative;
            .el-dialog__header { border-bottom: 1px solid #ccc; }
            .el-dialog__body { padding: 30px 0;height:100%;
                .dialog-content { padding: 0 20px;
                    height: 500px;
                    overflow-y: auto;
                    .step-line { height: 40px;display: flex;
                        .el-steps {width: 100%; margin-top: 0!important; height: 40px; line-height: 40px; padding: 0 8% !important;
                            .el-step {
                                &.is-simple {
                                    .el-step__head {line-height: 1;
                                        &.is-success {color: #5681C2;  border-color: #5681C2;}
                                    }
                                }
                                .el-step__title {
                                    &.is-success { color: #5681C2; }
                                }
                            }
                        }
                        .img-step {display: flex; width: 100%; height: 40px; background-size: 100% 100%!important;
                            &.one {background: url("./images/step-1.png");}
                            &.two {background: url("./images/step-2.png");}
                            &.three {background: url("./images/step-3.png");}
                            &.four {background: url("./images/step-4.png");}
                            .item-ccc {display: flex; width: 25%; align-items: center;
                                div {  }
                                img {height: 20px; width: 20px; margin-left: 70px;margin-right: 20px;}
                                &.active {color: #5681C2;}
                            }
                        }
                    }
                    .hot-search { display: flex; font-size: 12px; height: 50px; align-items: center;
                        .hot-name { color: #5681C2; margin-right: 10px; width: 30px;}
                        .zimuList { display: flex; justify-content: space-between; width: 100%; font-weight: bolder;
                            div {cursor: pointer; color: #6E6E6E;
                                &.active {color: #5681C2;}
                            }
                        }
                    }
                }
                .selected-type {
                    height: auto;
                    display: flex;
                    align-items: center;
                    /*margin-bottom: 20px;*/
                    align-items: center;
                    div {
                        &:first-child {width: 100px; height: 50px; line-height: 50px;}
                    }
                    .item-row {
                        display: flex;
                        flex-wrap: wrap;
                        .item {
                            width: 120px;
                            height: 30px;
                            line-height: 30px;
                            border: 1px solid #5681C2;
                            color: #6E6E6E;
                            text-align: center;
                            margin-right: 20px;
                            display: flex;
                            align-items: center;
                            &:nth-child(6) {margin-right: 0;}
                            div {width: 80%;}
                        }
                    }
                }
                .selected-slect {height: 30px;margin-bottom: 10px;text-align: left;
                    .el-select { height: 24px; width: 100px;
                        .el-input--suffix { height: 24px;
                            .el-input__inner {height: 100%}
                        }

                    }
                }
                .selected-list {border-top: 1px solid #ccc; padding-top: 10px; display: flex; flex-wrap: wrap;
                    .item-col {
                        padding: 0 10px;
                        min-width: 16.8%;
                        height: 50px;
                        line-height: 50px;
                        border: 1px solid #DBE4ED;
                        color: #6E6E6E;
                        text-align: center;
                        margin-right: 10px;
                        margin-bottom: 10px;
                        cursor: pointer;
                        &:hover {
                            border-color: #5681C2;
                        }
                        &:nth-child(5n) {margin-right: 0;}
                        &.active { border: 1px solid #5681C2; }
                    }
                }
                .button-list { position: absolute; left: 0; right: 0; bottom: 15px; margin: auto;height: 30px; width: 84px;
                    button {height: 30px; width: 84px; background-color: #5681C2; color: #fff; padding: 0;}
                }
            }
        }

    }

    .page-container {
        height: auto;
        overflow: hidden;
        margin: 0 auto;
        padding: 10px 20px;
        .fr {
            float: right;
        }
        .index-table-footer {
            text-align: center;
            margin-top: 40px;
            button {
                width: 84px;
            }
            button:first-child {
                margin-right: 50px;
            }
            button:last-child {
                margin-left: 50px;
            }
        }
        .work-content {
            height: auto;
            overflow: hidden;
            border-radius: 2px;
            padding: 10px 15px 15px;
            background: #fff;
            margin-bottom: 10px;
            .work-header {
                height: 33px;
                padding: 5px 0;
                .select-car {
                    color: #6E6E6E;
                    display: flex;
                    height: 30px;
                    align-items: center;
                    .car-type {
                        margin-right: 10px;
                    }
                }
                .header-title {
                    display: inline-block;
                    height: 30px;
                    line-height: 30px;
                    color: #999;
                    font-weight: bold;
                    font-size: 14px;
                    padding-left: 10px;
                    background: url(./images/icon_arrow-right.png) no-repeat left center;
                }
                .header-tool {
                    display: inline-block;
                    .el-button--mini {
                        border: 1px solid #5681C2;
                        border-radius: 4px;
                        font-size: 14px;
                    }
                }
            }
            .work-header-line {
                border-bottom: 1px solid #dbe4ed;
            }
            &.fix-tree-table { display: flex;
                .menu-tree { width: 178px; margin-right: 10px;
                    .el-menu {
                        border: 1px solid #C1CCD8;
                        width: 178px;
                        height: 283px;
                        overflow-y: auto;
                        overflow-x:hidden;
                        .el-menu-item { border-bottom: 1px solid #C1CCD8;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            height: 40px ;
                            line-height: 40px;
                            &:first-child {background-color: #FAFCFF;}
                            span {
                                overflow-x: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                            }
                            &.is-active {
                                background-color: #5882BF !important;
                                color: #fff !important;
                            }
                            /*&:last-child{ border-bottom: none; }*/
                        }
                        .el-submenu {
                            border-right: none;
                            border-left: none;
                            border-bottom: 1px solid #C1CCD8;
                            .el-submenu__title {
                                height: 40px ;
                                line-height: 40px ;
                            }
                            .el-menu {
                                border-right: none;
                                border-left: none;
                                border-bottom: none;
                                height:auto;
                                .el-menu-item {
                                    margin-top: 1px;
                                    border-bottom: 1px solid #C1CCD8;
                                    height: 40px ;
                                    overflow: hidden;
                                    line-height: 40px ;
                                    min-width: 170px;
                                    &:last-child {border-bottom: none;}
                                    &.is-active {
                                        background-color: #5882BF !important;
                                        color: #fff !important;
                                    }
                                }
                            }

                        }
                    }
                }
                .work-table {
                    position: relative;
                    padding-top: 15px;
                    height: auto;
                    overflow: hidden;
                    .search-item { height: 30px; align-items: center;display: flex;justify-content: flex-end; margin-bottom: 16px;
                        .el-input {height: 28px; width: 390px;
                            .el-input__inner {  height: 100%;}
                            .el-input__suffix-inner {
                                .el-input__icon {line-height: 30px;}
                            }
                        }
                        .el-button.search { margin-left: 10px; height: 28px; line-height: 28px;padding: 0 10px; }
                    }
                    .tabs-info {
                        height: 30px;
                        line-height: 30px;
                        padding: 5px 0 5px 0;
                        border-top: 1px solid #C1D1E5;
                    }
                    .ab-top-box {
                        position: absolute;
                        top: 10px;
                        right: 0;
                        z-index: 99;
                        .el-button--primary {
                            padding: 5px 10px;
                            font-size: 14px;
                            color: #FFFFFF;
                        }
                    }
                    .el-table {
                        margin-left: 0;
                        .el-table__header-wrapper {
                            tr {height: 62px;}
                        }
                    }
                    .el-table .cell {
                        font-size: 12px;
                        color: #6E6E6E;
                    }
                    .el-table th > .cell {
                        font-size: 14px;
                        color: #2E2E2E;
                    }
                    .el-button--text {
                        color: #5681C2;
                    }
                    .el-table td {
                        padding: 5px 0;
                    }
                    .el-input-number--mini {
                        width: 120px;
                    }
                    .el-input-number__decrease,
                    .el-input-number__increase {
                        top: 2px;
                    }
                    .table-footer {
                        border: 1px solid #DBE4ED;
                        height: 20px;
                        padding: 10px 20px;
                        border-radius: 0 0 4px 4px;
                        div {
                            display: inline-block;
                            float: left;
                            line-height: 20px;
                            margin-right: 100px;
                            span {
                                display: inline-block;
                                float: left;
                                height: 20px;
                                font-size: 14px;
                                color: #2E2E2E;
                                font-weight: bold;
                            }
                            p {
                                margin-left: 60px;
                                font-size: 12px;
                                color: #C91523;
                            }
                        }
                        .add-link {
                            float: right;
                            margin-right: 50px;
                            .a-tag-link {
                                font-size: 12px;
                                color: #5681C2;
                                cursor: pointer;
                            }
                        }
                    }
                    .more-table-bottom-wraper {
                        padding: 30px 0;
                        font-size: 14px;
                        color: #6E6E6E;
                        text-align: center;
                        line-height: 14px;
                        img {margin-left: 5px;}
                    }
                    .table-bottom-wraper {
                        padding: 16px 0;
                        height: 20px;
                    }
                }
            }

            .type-table {
                height: 252px;
                .flex-table { display: flex;justify-content: flex-end;
                    .search-item {
                        height: 40px;
                        width: 450px;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        .el-input__inner {height: 28px;}
                        .el-button {height: 28px !important; margin-left: 10px; padding: 0 10px;}
                    }
                }
                .item-row {
                    height: 50px;
                    line-height: 50px;
                    color: #6E6E6E;
                    font-size: 14px;
                    margin: 0 14px;
                    display: flex;
                    border-bottom: 1px solid #C1D1E5;
                    &.margin {
                        margin: 0;
                        padding: 0 14px;
                        border-top: 1px solid #C1D1E5
                    }
                    .left-item {
                        border-right: 1px solid #C1D1E5;
                        width: 160px;
                    }
                    .right-item {
                        display: flex;
                        width: 100%;
                        justify-content: space-between;
                        overflow: auto;
                        .more {
                            color: #BFBDBD;
                            min-width: 44px;
                            &.active {
                                color: #2E2E2E;
                            }
                        }
                        .eee {
                            width: 100%;
                            display: flex;
                            .item-col {
                                width: 100px;
                                cursor: pointer;
                                &.active {
                                    color: #5681C2;
                                }
                                &:first-child {
                                    margin-left: 20px;
                                }
                            }
                        }

                    }
                }
            }
            &.fix-search {
                height: 50px;
                display: flex;
                align-items: center;
                .el-input {
                    width: 240px;
                    height: 28px;
                    .el-input__inner {
                        height: 100%;
                    }
                }
                .el-radio-group {
                    margin-left: 10px;
                    height: 28px;
                    .el-radio-button {
                        height: 100%;
                        .el-radio-button__inner {
                            padding: 6px 20px;
                        }
                        &.is-active {
                            .el-radio-button__inner {
                                background-color: #5681C2;
                                border-color: #5681C2;
                            }
                        }
                    }
                }
            }
            .inputCont {
                display: flex;
                height: auto;
            }
            .el-table {
                margin-left: 1px;
                thead{ color: #2E2E2E; }
            }
            .work-wraper {
                height: auto;
                overflow: hidden;
                .grid-content {
                    height: auto;
                    overflow: hidden;
                    border-top: 1px solid #dbe4ed;
                    border-left: 1px solid #dbe4ed;
                    border-radius: 4px;
                    .grid-item {
                        display: flex;
                        align-items: center;
                        height: auto;
                        min-height: 40px;
                        padding: 0 15px;
                        border-bottom: 1px solid #dbe4ed;
                        border-right: 1px solid #dbe4ed;
                        &:last-child {
                            .item-title {width: 129px}
                        }
                        .item-title {
                            color: #2e2e2e;
                            font-size: 14px;
                            float: left;
                            width: 73px;
                        }
                        p {
                            color: #6e6e6e;
                            font-size: 12px;
                            word-break: break-all;
                        }
                    }
                }
                .checkbox-button-box {
                    height: auto;
                    overflow: hidden;
                }
                .work-table-footer {
                    padding-top: 40px;
                    text-align: center;
                }
            }
        }
    }
</style>



